<!-- 时间节点设置 -->
<template>
    <div class="timeset-container">
        <div class="timeset-header">
            <span class="timeset-title">设置评估流程时间节点</span>
            <div class="btn-area">
                <a-button type="primary" @click="publishNewRound"
                    style="background-color: #68BBC4;">发布新一轮评估时间</a-button>
                <!-- 自定义评估周期名字弹窗 -->
                <a-modal v-model:open="openCycleName" title="定义新一轮评估名称" okText="开始设置评估时间" @ok="handleCycleNameOk">
                    <div v-if='!isPushRes' style="color: red;margin-bottom: 5px;">注意：当前评估还未完成！若仍要发起新一轮评估请继续操作即可~</div>
                    <a-input v-model:value="cycleName" placeholder="例如：2023-2024年第一期评估"></a-input>
                </a-modal>
                <a-button type="primary" @click="editTime" ghost>修改当前时间节点</a-button>
            </div>
        </div>

        <div class="timeset-content">
            <!-- 时间设置区域 -->
            <div class="timeset-area">
                <a-form>
                    <a-row :gutter="16" style="margin-bottom: 20px;">
                        <a-form-item label="建立评估矩阵关系" :label-col="{ span: 8 }" :rules="[{ required: true, message: '请填写！' }]" style="margin-right:30px">
                            <a-range-picker v-model:value="formTime.relBuildingTime" :disabled="!isEdit"
                                style="width: 100%;" />
                        </a-form-item>
                        <a-form-item label="上级评估/周边评估" :label-col="{ span: 8 }" :rules="[{ required: true, message: '请填写！' }]">
                            <a-range-picker v-model:value="formTime.evaluationTime" :disabled="!isEdit"
                            style="width: 100%;" />
                        </a-form-item>
                    </a-row>
                    <a-row :gutter="16">
                        <a-form-item label="结果沟通" :rules="[{ required: true, message: '请填写！' }]" style="margin-right:30px">
                            <a-range-picker v-model:value="formTime.resCommunicaTime" :disabled="!isEdit"
                            style="width: 100%;" />
                        </a-form-item>
                        <a-form-item label="结果发布" :rules="[{ required: true, message: '请填写！' }]">
                            <a-range-picker v-model:value="formTime.resultReleaseTime" :disabled="!isEdit"
                            style="width: 100%;" />
                        </a-form-item>
                    </a-row>
                </a-form>
                <div class="save-btn">
                    <a-button type="primary" html-type="submit" v-if="isEdit" @click="handleSubmit">发布 / 修改</a-button>
                </div>
            </div>
        </div>
    </div>
</template>


<script setup>
import { ref, createVNode, reactive, onMounted } from "vue"
import { Modal } from 'ant-design-vue'
import { ExclamationCircleOutlined } from '@ant-design/icons-vue'
import dayjs from "dayjs";
import { message } from 'ant-design-vue'
import { getStepTimeApi, getEditTimeApi } from "@/api/scoreTimeSet"

// 自定义评估周期名字相关配置
const openCycleName = ref(false)
const cycleName = ref('')
const handleCycleNameOk = () => {
    if (cycleName.value === '') {
        message.warning('请输入评估周期名称！')
        return
    }
    openCycleName.value = false
    message.success('名称设置成功，请继续设置时间节点！')
    if (isEdit.value === true) {
        btnType.value = '1'
    } else {
        isEdit.value = !isEdit.value
        btnType.value = '1'
    }
}

const formTime = reactive({
    relBuildingTime: [dayjs('2024-01-01'), dayjs('2024-01-02')],
    evaluationTime: [dayjs('2024-01-05'), dayjs('2024-01-10')],
    resCommunicaTime: [dayjs('2024-02-01'), dayjs('2024-02-05')],
    resultReleaseTime: [dayjs('2024-03-01'), dayjs('2024-03-05')]
})

const btnType = ref('0')

const isEdit = ref(false)
const publishNewRound = () => {
    openCycleName.value = true
}

const editTime = () => {
    isEdit.value = !isEdit.value
    btnType.value = '2'
}

// 获取当前时间节点
const currentSession = ref()
const isPushRes = ref(false);
const getStepTime = () => {
    getStepTimeApi()
        .then(res => {
            if (res.data.code === 200) {
                formTime.relBuildingTime = [
                    dayjs(res.data.data.evaluateStep[0].startDate),
                    dayjs(res.data.data.evaluateStep[0].endDate)
                ]
                formTime.evaluationTime = [
                    dayjs(res.data.data.evaluateStep[1].startDate),
                    dayjs(res.data.data.evaluateStep[1].endDate)
                ]
                formTime.resCommunicaTime = [
                    dayjs(res.data.data.evaluateStep[2].startDate),
                    dayjs(res.data.data.evaluateStep[2].endDate)
                ]
                formTime.resultReleaseTime = [
                    dayjs(res.data.data.evaluateStep[3].startDate),
                    dayjs(res.data.data.evaluateStep[3].endDate)
                ]

                for (let i = 0; i <= res.data.data.evaluateStep.length - 1; i++) {
                    if (res.data.data.evaluateStep[i].enable === 1) {
                        currentSession.value = i + 1 + '';
                        if (currentSession.value === '4') {
                            isPushRes.value = true
                        }
                    }
                }
            } else {
                console.log('获取失败！', res.data.msg);
            }
        })
        .catch(err => {
            console.log('获取失败！', err);
        });
};

// 调用api-提交新时间
const handleSubmit = () => {
    const title = ref('')
    if (btnType.value === '1') {
        title.value = '确定要发布新一轮评估吗？'
    } else if (btnType.value === '2') {
        title.value = '确定要修改评估时间吗？'
    } else {
        title.value = 'undefined'
    }
    // 检查四项时间是否都填写完整
    const isFormComplete = formTime.relBuildingTime && formTime.evaluationTime &&
        formTime.resCommunicaTime && formTime.resultReleaseTime
    if (!isFormComplete) {
        // 提示用户填写完整
        message.warning('请将所有时间填写完整哦~');
        return
    }
    // 确认弹窗
    Modal.confirm({
        title: title.value,
        icon: createVNode(ExclamationCircleOutlined),
        onOk() {
            // 预处理8个时间字段
            const newtime = [
                ...formTime.relBuildingTime.map(date => date.format('YYYY-MM-DD')),
                ...formTime.evaluationTime.map(date => date.format('YYYY-MM-DD')),
                ...formTime.resCommunicaTime.map(date => date.format('YYYY-MM-DD')),
                ...formTime.resultReleaseTime.map(date => date.format('YYYY-MM-DD')),
            ];
            if (btnType.value === '1') {
                // 调用api-发布新一轮评估时间
                getEditTimeApi(true, newtime, cycleName.value).then(res => {
                    if (res.data.code === 200) {
                        message.success('发布成功！')
                        // 刷新页面
                        setTimeout(() => {
                            location.reload();
                        }, 500); // 延迟0.5秒刷新页面
                    } else {
                        message.error('发布失败！' + res.data.msg);
                        // 刷新页面
                        setTimeout(() => {
                            location.reload();
                        }, 1000); // 延迟1秒刷新页面
                    }
                }).catch(err => {
                    console.log(err)
                })
            } else {
                getEditTimeApi(false, newtime).then(res => {
                    if (res.data.code === 200) {
                        message.success('修改成功！')
                        // 刷新页面
                        setTimeout(() => {
                            location.reload();
                        }, 500); // 延迟0.5秒刷新页面
                    } else {
                        message.error('修改失败！' + res.data.msg);
                        // 刷新页面
                        setTimeout(() => {
                            location.reload();
                        }, 500); // 延迟0.5秒刷新页面
                    }
                }).catch(err => {
                    console.log(err)
                })
            }
            isEdit.value = false
        },
        // eslint-disable-next-line @typescript-eslint/no-empty-function
        onCancel() { },
    });
}

onMounted(() => {
    getStepTime()
})
</script>

<style lang="less" scoped>
.timeset-container {
    margin: 30px 10px 10px 10px;

    .timeset-header {
        display: flex;
        justify-content: space-between;

        .timeset-title {
            display: block;
            font-size: 16px;
            font-weight: bold;
            margin-bottom: 15px;
        }

        .btn-area {
            margin-bottom: 10px;

            &> :nth-child(1) {
                margin-right: 10px;
            }
        }
    }

    .timeset-content {
        border-top: 1px dashed #3a8ee6; // 边框为蓝色虚线
        background-color: #e6f7ff; // 浅蓝色背景
        padding: 10px;

        .timeset-area {
            max-width: 100%;
            overflow: hidden;
            box-sizing: border-box;
            margin: 5vh;
            border: 1px solid #d9d9d9;
            box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
            border-radius: 5px;
            padding: 5vh;

            .save-btn {
                display: flex;
                justify-content: center;
                align-content: center;
                margin-top: 20px;
            }
        }
    }
}
</style>