import axios from "axios";

//获取该时间段内的执行情况数据
const getHeadnoteApi = () =>
  new Promise((resolve, reject) => {
    axios({
      method: "get",
      url: "/api/evaluate/table",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((response) => {
        resolve(response);
      })
      .catch((error) => {
        reject(error.response);
      });
  });

//获取该时间段内的执行情况数据
const getHeadnoteUpdateApi = (meanings, principles, openingRemarks, title) =>
  new Promise((resolve, reject) => {
    let data = {
      meanings: meanings,
      principles: principles,
      openingRemarks: openingRemarks,
      title: title
    };
    axios({
      method: "post",
      url: "/api/evaluate/table/update",
      headers: {
        "Content-Type": "application/json",
      },
      data: JSON.stringify(data),
    })
      .then((response) => {
        resolve(response);
      })
      .catch((error) => {
        reject(error.response);
      });
  });

export { getHeadnoteApi, getHeadnoteUpdateApi };
